import React, {useState} from "react";
import Header from "../components/Header/Header.jsx"
import Footer from "../components/Footer/Footer";
import plus from "../img/Vector.svg"
import Modal from "../components/Modal/Modal";
import Carousel from "../components/Carousel/Carousel";
import UserCard from "../components/UserCard/UserCard";
import ToastSucces from "../components/ToastSucces/ToastSucces";
import img1 from '../components/FeedbackCard/img/1.png'
import avatar from '../components/FeedbackCard/img/Camera.svg'
import img2 from '../components/FeedbackCard/img/3.png'
import img3 from '../components/FeedbackCard/img/4.png'

const About = () => {

    const [modal, setModal] = useState(false)
    const [toast, setToast] = useState(false)

    const user = {name:'', surname:'', city:'', sex:'', age:'21', pets: '', date: '20.09.2000',
        info: ' !   ,  21 .          ,       -   ,        .'
    }
    const [reviewers] = useState([
        { id: 1, name:"", surname: '', avatar: img1, date: '08.01.2022', body: ' ,            .  KPI        -      ,    .  -   ,     ,   .    .'},
        { id: 2, name:"", surname: '', avatar: avatar, date: '08.01.2022', body: '   ,   ,      .      ,      ,    .      .         .'},
        { id: 3, name:"", surname: '', avatar: img2, date: '26.12.2021', body: '    ilink  .      :    ,   ,   .        ,    ,  ,   .     .  ,      ilink.'},
        { id: 4, name:"", surname: ' ', avatar: img3, date: '16.12.2021', body: '       ,      HR-    .       HR-.   .  ,   .    .   ,   ,    .     .  , .'}
      ])
    
    const prev = () => {
        const prevBtn = document.querySelector('.prev')
        prevBtn.click();
    }

    const next = () => {
        const nextBtn = document.querySelector('.next')
        nextBtn.click();
    }
  return (
    <div className="App">
                <div className="wrapper">
                    <Header user={user}/>
                    <main className="main">
                        <div className="content">
                            <h3 className="mainText"> <br/> !</h3>
                            <UserCard user={user}/>
                            <div className="review">
                                <div className="review__container">
                                    <div className="inline">
                                        <h2 className="reviewHeader"></h2>
                                        <button className='reviewBtn' onClick={()=>setModal(true)}>
                                            <img className='btnIcon' src={plus} alt='profile'/>
                                            <span className='inner'> </span>
                                        </button>
                                    </div>
                                    <Modal setShow={setToast} visible={modal} setVisible={setModal}></Modal>
                                    <Carousel reviewers={reviewers}/>
                                </div>
                                <button className="prevBtn" onClick={prev}><div className="prevInner"></div></button>
                                <button className="nextBtn" onClick={next}><div className="nextInner"></div></button>
                            </div>
                        </div>
                    </main>
                    <Footer/>
                    <ToastSucces 
                        title="!"
                        body="      :)"
                        show={toast} 
                        setShow={setToast}       
                    />
                </div>
            </div>
  )
}

export default About